import ReactMarkdown from 'react-markdown';
import labels from '@openagenda/labels/form-schemas/index.js';
import { MoreInfo } from '@openagenda/react-shared';

function _target(link) {
  if (!link || link.indexOf('mailto:') !== -1) return '_self';

  return '_blank';
}

export default function Help({ lang, label, content, link }) {
  if (content) {
    return (
      <MoreInfo
        content={(
          <ReactMarkdown disallowedElements={['p']} unwrapDisallowed>
            {content}
          </ReactMarkdown>
        )}
      >
        <a target={_target(link)} href={link}>
          {label || labels.help[lang]}
        </a>
      </MoreInfo>
    );
  }

  return (
    <a className="margin-right-xs" target={_target(link)} href={link}>
      {label || labels.help[lang]}
    </a>
  );
}
